import React, { useState, useEffect } from 'react'
import styled from 'styled-components'
import axios from 'axios'

const Main: React.FC = () => {
  const [data, setData] = useState([]);
  const [count, setCount] = useState(0);
  useEffect(() => {
      axios.get("https://admin.tomars.me/api/getSymbolList?q=")
      .then(res => {
        console.log(1);
        setData(res.data);
      })
      .catch(e => {
        console.log('error:' + e);
      })
      const id = setInterval(()=>{
        setCount(c=>c+1);
      },3000); 
      console.log(2);
      return ()=> clearInterval(id);
  },[count]);

  return (
    <MainAll>
      <MainTop>
        <div>MEDX-SWAP</div>
        <div>本次使用时长 {count*3} s</div>
      </MainTop>
      {data.length !== 0 ? (
        data.map((item)=>(
        <Lab>
          <Coin>
            <Image
            src={item['token_a']['logoURI']}
            />
            <Name>
              {item['token_a']['symbol']}
            </Name>
            <Price>
              {item['price']+' $ '}
            </Price>
          </Coin>
        </Lab>
        ))
      ) : (
          <div>
            Loading...
          </div>
        )}
    </MainAll>
  )
}

const MainAll = styled.div`
  margin-top: 2em;
  width: 100%;
  border-radius: 5px;
  border: 1px solid #fcb2bf;
  
`

const MainTop = styled.div`
  display: felx;
  justify-content: space-between;
  width: 100%;
  height: 40px;
  font-size: 25px; 
  text-align: left;
  line-height: 40px;
  border: 1px solid #511e78;
  border-radius: 4px;
`

const Lab = styled.div`
  display: flex;
  justify-content: space-between;
  width: 100%;
  height: 80px;
  font-size: 20px;
  line-height: 80px;
  border: 1px solid #8b2f97;
  border-radius: 5px;
`

const Coin = styled.div`
   display: flex;
   margin-left: 100px;
   width: 900px;
   height: 80px;
   justify-content: space-between;
   @media (max-width: 768px) {
    width: 100%;
    margin-left: 5px;
  }
`;

const Image = styled.img`
  margin-top: 20px;
  width: 40px;
  height: 40px;
`;


const Name = styled.div`
  margin-left: 10px;
  @media (max-width: 768px) {
    margin-left:0px;
  }
`
const Price = styled.div`
  margin-left: 100px;
  color: #1c0c59;
  @media (max-width: 768px) {
    margin-left: 0px;
  }
`

export default Main;